import React, { Component } from 'react';
import {Link} from 'react-router-dom'

export default class Menu extends Component {
	render() {
		return(
			<div>
				<ul>
					<li><Link to='/'></Link></li>
					<li><Link to='/lists'></Link></li>
					<li><Link to='/about'></Link></li>
				</ul>
			</div>
			)
	}
}



